body {
  background-color: hsl(27, 66%, 92%);
  display: flex;
  justify-content: center;
  align-items: center;

  height: 100vh;
  margin: 0;
  font-family: 'DM Sans', sans-serif;
}

* {
  box-sizing: border-box;
}
p {
  margin: 0;
}
.box-red {
  display: flex;
  padding: 20px 25px;
  color: white;
  justify-content: space-between;
  border-radius: 10px;
  width: 400px;
  height: 90px;
  background-color: hsl(10, 79%, 65%);
}
.box-red p:nth-of-type(2) {
  font-size: 25px;
  font-weight: 700;
}

.box-white {
  padding: 20px 25px;
  color: hsl(25, 47%, 15%);

  border-radius: 10px;
  margin-top: 20px;
  width: 400px;
  height: 360px;
  background-color: hsl(33, 100%, 98%);
}
.box-white p:nth-of-type(1) {
  font-size: 25px;
  font-weight: 700;
}
img {
  width: 50px;
  height: auto;
  display: block;
}

.box-bot {
  display: flex;
  justify-content: space-between;
}
.left2 p:nth-of-type(1) {
  color: hsl(28, 10%, 53%);
  font-weight: 400;
  font-size: 15px;
}
.left2 p:nth-of-type(2) {
  font-weight: 700;
  font-size: 40px;
}

.right1 {
  display: flex;
  flex-direction: column;
  justify-content: end;
  padding-bottom: 8px;
}
.right1 p:nth-of-type(1) {
  font-size: 15px;
  text-align: right;
}
.right1 p:nth-of-type(2) {
  color: hsl(28, 10%, 53%);
}
.box-bars {
  width: 100%;
  height: 185px;
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  border-bottom: 4px solid hsl(27, 66%, 92%);
  padding-bottom: 20px;
  justify-content: center;
}
.bar-set {
  display: flex;
  justify-content: end;
  flex-direction: column;
  text-align: center;
  height: 100%;
  width: 100%;
  overflow: visible;
  align-items: center;
}
.box-day {
  background-color: hsl(25, 47%, 15%);
  width: 100%;
  margin-bottom: 10px;
  border-radius: 5px;
  height: 30px;
  color: white;
  font-size: 12px;
  font-weight: 700;
  text-align: center;
  padding-top: 6px;
  visibility: hidden;
}
.bar-set:hover .box-day {
  visibility: visible;
}
.bar-set:hover .bar {
  filter: brightness(120%);
}

.bar {
  width: 80%;
  border-radius: 5px;
  background-color: hsl(10, 79%, 65%);
  height: 0;
  transition: height 0.5s ease;
}

.day {
  margin-top: 5px;
  color: hsl(28, 10%, 53%);
  font-weight: 400;
  font-size: 15px;
}

@keyframes fadeInHeight {
  0% {
    height: 0;
  }
  100% {
    height: /* altura deseada */ ;
  }
}

.bar {
  animation: fadeInHeight 2s ease;
}
